import React, {Component} from 'react'
import {Link} from "react-router-dom";

class Welcome extends Component{
    render() {
        return (
            <div>
                <div>
                    欢迎进入点餐团队下面是本次的安排
                </div>
                <div>
                    <Link to={'/overview'}>点击进入</Link>
                </div>
            </div>
        )
    }
}
export default Welcome