import React, {Component} from 'react'
import {Link} from 'react-router-dom'
class Overview extends Component{
    render() {
        return (
            <ul>
                <li><Link to={'/user'}>人员安排</Link></li>
                <li><Link to={'/requirement'}>项目要求</Link></li>
                <li><Link to={'/plan'}>项目规划</Link></li>
                <li><Link to={'/important'}>项目技术点</Link></li>
            </ul>
        );
    }
}
export default Overview