import React, {Component} from 'react'
import {HashRouter,Switch,Route} from 'react-router-dom'
import Overview from '../overview'
import Welcome from '../Welcome'
import User from '../User'
import Requirement from '../Requirement'
import Plan from '../Plan'
import Important from '../Important'
class App extends Component{
    render(){
        return(
            <HashRouter>
                <Switch>
                    <Route exact path={"/"}><Welcome/></Route>
                    <Route path={"/overview"}><Overview></Overview></Route>
                    <Route path={"/user"}><User></User></Route>
                    <Route path={"/Requirement"}><Requirement></Requirement></Route>
                    <Route path={"/plan"}><Plan></Plan></Route>
                    <Route path={"/important"}><Important></Important></Route>
                </Switch>
            </HashRouter>
        )
    }
}
export default App